@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap);
.app {
  display: flex;
  justify-content: left;
  /* justify-content needs display first */
}

.app-wrapper {
  /* 21b045 */
  position: relative;
  overflow-x: hidden;
  /* overflow-y: hidden; */
  width: 100%;
  height: inherit;
}

.paddles {
  position: relative;
  bottom: 30px;
  left: 38%;
  display: flex;
}

.paddle {
  background: none;
  font-size: 30px;
  border: none;
  padding: 10px;
  color: #ffffff;
}

.paddle:hover {
  cursor: pointer;
}
.paddle.left-paddle {
  padding-right: 30px;
}
.paddle.right-paddle {
  padding-left: 30px;
}

.newPageButton {
  background: none;
  color: white;
  font-size: 20px;
  font-weight: 200;
  padding: 10px;
  border: white solid 2px;
  border-radius: 10px;
}

.newPageButton:hover {
  cursor: pointer;
}
/* swatches css */
.swatches {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  color: white;
  position: relative;
}

.swatch {
  flex: 0 0 30%;
  font-size: 15px;
  max-width: 70px;
  height: 70px;
  margin: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.swatch:hover {
  cursor: pointer;
}

.swatch.one {
  background-image: linear-gradient(to bottom right, #80ced7, #007ea7);
}
.swatch.two {
  background-image: linear-gradient(to bottom right, #67b26f, #4ca2cd);
}
.swatch.three {
  background-image: linear-gradient(to bottom right, #c6ffdd, #fbd786);
}
.swatch.four {
  background-image: linear-gradient(to bottom right, #7b4397, #dc4024);
}
.swatch.five {
  background-image: linear-gradient(to bottom right, #654ea3, #eaafc8);
}
.swatch.six {
  background-image: linear-gradient(to bottom right, #e58c8a, #f3d2d6);
}
.swatch.seven {
  background-image: linear-gradient(to bottom right, #870000, #190a05);
}
.swatch.eight {
  background-image: linear-gradient(to bottom right, #ff9966, #ffedbc);
}
.swatch.nine {
  background-image: linear-gradient(to bottom right, #2c3e50, #bdc3c7);
}

/* List of Pages */

.parentList {
  overflow: hidden;
  width: 100%;
  height: 55vmin;
}

.listOfPages {
  padding-right: 17px;
  box-sizing: content-box;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
}

.pageElement {
  cursor: pointer;
  padding: 7px;
  padding-left: 15px;
  margin: 10px;
  margin-left: 20px;
  border: 1px solid black;
  border-radius: 14px;
}

.pageElement:hover {
  background-color: #eeeeee;
  transition: ease 0.1s;
}

* {
  font-family: "Nanum Gothic", sans-serif;
}

.parentContainer {
  display: inherit;
  justify-content: center;
  min-width: 100%;
}

.container {
  display: inherit;
  margin: 80px;
}

.page {
  background-color: white;
  text-align: center;
  position: relative;
  width: 470px;
  height: 730px;
  box-shadow: -1px 1px 20px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.pageTitle {
  font-size: 30px;
  padding-top: 1em;
  border: none;
  text-align: center;
}

hr {
  color: #eeeeee;
  width: 200px;
}

.page .toDoList {
  text-align: left;
  height: 75%;
  padding: 20px;
  overflow-y: auto;
}

.page .footer {
  min-height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  bottom: 0;
}

.page .footer .newTaskInput {
  border: none;
  border-bottom: 1px solid #000000;
  width: 300px;
  margin: 10px;
  font-size: 17px;
  padding: 5px;
  outline: none;
}

.submitNewTask:hover {
  background: #eeeeee;
  transition: 0.2s;
}

.submitNewTask {
  cursor: pointer;
  width: 30px;
  height: 30px;
  border-radius: 15px;
  border: 1px #000000 solid;
}

.toDoItem {
  border: 1px #d3d3d3 solid;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 10px;
  position: relative;
  display: flex;
}

.toDoItem .toDoCheck {
  margin-right: 10px;
}

.toDo {
  display: inline-block;
  max-width: 300px;
}

.indivMenu {
  cursor: pointer;
  position: absolute;
}

.indivMenu.desc {
  right: 15px;
  width: 20px;
  height: 20px;
}
.indivMenu.del {
  right: 45px;
  width: 20px;
  height: 20px;
}

/* this is for the description */

.description {
  background: rgba(0, 0, 0, 0.2);
  padding: 10px;
  color: white;
  width: 260px;
  height: 710px;
  border-top-right-radius: 20px;
  text-align: center;
  justify-content: center;
  box-shadow: -1px 0 10px rgba(0, 0, 0, 0.2);
}

.nestedInputForm {
  display: flex;
  text-align: center;
  justify-content: center;
}

.description span {
  color: white;
  z-index: 1;
}

.description .descriptionProp {
  margin-top: 30px;
  color: #ffffff;
  background: none;
  padding: 10px;
  resize: none;
  width: 220px;
  height: 300px;
  border: white solid 1px;
  word-wrap: break-word;
  font-size: 12px;
}

.nestedAcceptButton {
  margin-top: 10px;
  cursor: pointer;
  width: 30px;
  height: 30px;
  border-radius: 15px;
  border: 1px #ffffff solid;
}

.nestedAcceptButton:hover {
  opacity: 0.8;
}

.nestedInput {
  border: none;
  background: none;
  justify-content: center;
  border-bottom: 1px solid #ffffff;
  height: 30px;
  width: 200px;
  margin: 10px;
  font-size: 14px;
  color: #ffffff;
  outline: none;
}

.nestedInput::-webkit-input-placeholder {
  color: #ffffff;
}

.nestedInput:-ms-input-placeholder {
  color: #ffffff;
}

.nestedInput::-ms-input-placeholder {
  color: #ffffff;
}

.nestedInput::placeholder {
  color: #ffffff;
}

.nestedItem {
  margin-left: 5px;
}

.groupCheckbox {
  display: flex;
  padding: 5px;
}

html,
body {
  height: 100%;
  margin: 0;
}

.grid-container {
  display: grid;
  grid-template-columns: 280px auto;
  overflow: hidden;
  height: 100%;
}

.menu {
  text-align: center;
  padding-top: 20px;
  padding-left: 4px;
  padding-right: 17px;
  box-sizing: content-box;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
}

